<template>
    <div class="form-container">
        <div class="header text-right">
            <img src="@/assets/mipango-logo.png" alt="Mipango Initiative Logo" class="logo" />
            <div class="title-container position-relative">
                <h2 class="display-1 font-weight-bold mb-2">SEMINA YA KIUCHUMI</h2>
                <h4 class="subtitle-1 text--secondary">15-21 Februari</h4>
            </div>
        </div>

        <v-card flat class="text-left">

            <p class="body-1 pa-4">
                Mipango Initiative ni juhudi inayolenga kuwapa vijana na wanawake stadi za kiuchumi na ujasiriamali
                ili
                kuwawezesha kujikwamua kiuchumi na kuchangia maendeleo ya uchumi wa nchi. Mafunzo yanayopatikana ni
                kwajili ya makundi yafuatayo:
            </p>

            <div class="pa-4 text-center">


                <v-dialog v-model="processing" max-width="320" persistent>
                    <v-list class="py-2" color="primary" elevation="12" rounded="lg">
                        <v-list-item prepend-icon="$vuetify-outline" title="Taarifa zinatumwa, subiri...">
                            <template v-slot:prepend>
                                <div class="pe-4">
                                    <img height="30" :alt="`slotProps.node.data.name`" :src="`favicon.ico`"
                                        class="cursor-pointer rounded-t-xl" style="
                      left: 0 !important;
                      top: -64px !important;
                      width: auto !important;
                    " />
                                </div>
                            </template>

                            <template v-slot:append>
                                <v-progress-circular color="primary" indeterminate="disable-shrink" size="16"
                                    width="2"></v-progress-circular>
                            </template>
                        </v-list-item>
                    </v-list>
                </v-dialog>
            </div>

            <MakundiList />


            <p class="body-1 px-6 font-weight-bold">Kujiunga na SEMINA hii tafadhali jaza taarifa zifuatazo:</p>
            <div class="px-4 mx-2  fg-orange font-weight-bold text-shadow">
                Chagua kundi lako hapa chini:
            </div>
            <div class="w3-row-padding">
                <div class="w3-col s4 pl-4">
                    <div class="w3-col s8">
                        <div class="spinner scroll-down position-absolute w3-hide-large" style="left: 190% !important;">
                            <a class="animate"></a>
                        </div>
                        <div class="spinner scroll-down position-absolute w3-hide-small"
                            style="right: -160% !important;"> <a class="animate"></a> </div>
                    </div>
                </div>
            </div>



            <div class="d-flex flex-wrap gap-3 justify-center justify-center pa-4 fg-black mt-8">


                <div v-for="(chip, index) in chips" :key="index">
                    <v-chip class="m-1" :color="selectedChip === chip.name ? 'primary' : 'black'"
                        @click="selectChip(chip.name)" outlined active rounded>
                        <v-icon v-if="selectedChip === chip.name">mdi-check</v-icon>
                        {{ chip.label }}
                    </v-chip>
                </div>
            </div>
            <v-form ref="form" @submit.prevent="onFinish">
                <v-container>
                    <!-- First Name and Last Name -->
                    <v-row>
                        <v-col cols="12" sm="6">
                            <v-text-field v-model="firstName" label="Jina la kwanza"
                                :rules="[(v) => !!v || 'Tafadhali jaza jina!']" outlined dense />
                        </v-col>
                        <v-col cols="12" sm="6">
                            <v-text-field v-model="lastName" label="Jina la mwisho"
                                :rules="[(v) => !!v || 'Tafadhali jaza jina la ukoo!']" outlined dense />
                        </v-col>
                    </v-row>

                    <!-- Age Input -->
                    <v-row>
                        <v-col cols="12" sm="6">
                            <v-text-field v-model="age" label="Umri" type="number"
                                :rules="[(v) => !!v || 'Tafadhali jaza jina la umri!']" outlined dense />
                        </v-col>
                        <v-col cols="12" sm="6">
                            <v-radio-group v-model="gender" row>
                                <v-radio label="M/Me" value="male" />
                                <v-radio label="M/Ke" value="female" />
                            </v-radio-group>
                        </v-col>
                    </v-row>



                    <!-- Phone Number and Gender -->
                    <v-row>
                        <v-col cols="12" sm="6">
                            <v-text-field v-model="phoneNumber" label="Simu" :rules="[
                                (v) => !!v || 'Tafadhali weka namba yako ya simu!',
                                (v) => /^\d{10}$/.test(v) || 'Namba ya simu lazima iwe na tarakimu kumi tu!'
                            ]" outlined dense />
                        </v-col>
                    </v-row>


                    <!-- Region Selector -->
                    <v-row>
                        <v-col cols="12" sm="6">
                            <v-select v-model="selectedRegion" :items="regions" item-title="name" item-value="id"
                                return-object single-line label="Chagua mkoa" @change="onRegionChange"></v-select>
                        </v-col>
                        <v-col cols="12" sm="6">
                            <v-select v-model="selectedDistrict" :items="filteredDistricts" item-title="name"
                                item-value="id" return-object single-line label="Chagua wilaya"
                                :disabled="!selectedRegion"></v-select>
                        </v-col>
                    </v-row>

                    <!-- Submit Button -->
                    <v-row>
                        <v-col cols="12" class="text-center">
                            <v-btn type="submit" color="primary" :disabled="!isFormValid">Tuma taarifa</v-btn>
                        </v-col>
                    </v-row>
                </v-container>
            </v-form>
        </v-card>

        <!-- Ant Design Dialog -->
        <a-modal v-model:visible="dialogVisible" :title="dialogTitle" @ok="handleDialogOk">
            <p>{{ dialogMessage }}</p>
        </a-modal>
    </div>
</template>




<script>
import { defineComponent } from "vue";
import { db } from "@/firebase";
import { collection, addDoc } from "firebase/firestore";
import MakundiList from "./MakundiList.vue";
import { regions, districts } from '../assets/regionsdata.js';

export default defineComponent({
    data() {
        return {
            firstName: "",
            lastName: "",
            phoneNumber: "",
            gender: "",
            age: "",
            selectedRegion: { id: 2, name: "Dar es Salaam" },
            selectedDistrict: null,
            processing: false,
            isFormValid: false,
            regions,
            districts,
            selectedChip: null,
            chips: [
                { name: "ulinzi", label: "Ulinzi" },
                { name: "ujenzi", label: "Ujenzi" },
                { name: "madini", label: "Madini" },
                { name: "afya", label: "Afya" },
                { name: "makundi-maalum", label: "Makundi Malumu" },
                { name: "elimu", label: "Elimu" },
                { name: "uchukuzi-na-usafirishaji", label: "Uchukuzi na Usafirishaji" },
                { name: "utalii", label: "Utalii" },
                { name: "usafirishaji", label: "Usafirishaji" },
                { name: "kilimo/ufugaji/uvuvi", label: "Kilimo, Ufugaji na Uvuvi" },
                { name: "sanaa-na-michezo", label: "Sanaa na Michezo" },
                { name: "diplomasia", label: "Diplomasia" },
                { name: "tamaduni", label: "Tamaduni" },
                { name: "habari-na-mawasiliano", label: "Habari na Mawasiliano" },
            ],
            dialogVisible: false,
            dialogTitle: "",
            dialogMessage: "",
            options: [
                { value: "ujasiriamali", label: "Ujasiriamali na Usimamizi wa Biashara" },
                { value: "kiufundi", label: "Stadi za Kiufundi na Ufundi" },
                { value: "kilimo", label: "Kilimo na Ufugaji" },
                { value: "ict", label: "Teknolojia ya Habari na Mawasiliano (ICT)" },
                { value: "fedha", label: "Usimamizi wa Fedha na Uwekezaji" },
            ],
        };
    },
    watch: {
        selectedRegion(newValue) {
            console.log(newValue);
            this.selectedDistrict = null;
        },
        selectedChip(newValue) {
            if (newValue && this.firstName.length > 0 && this.lastName.length > 0 && this.phoneNumber.length > 0) {
                this.isFormValid = true;
            }
        },
        lastName(newValue) {
            if (newValue.length > 0 && this.phoneNumber.length > 0 && this.firstName.length > 0) {
                this.isFormValid = true;
            }
        },
        firstName(newValue) {
            if (newValue.length > 0 && this.phoneNumber.length > 0 && this.lastName.length > 0) {
                this.isFormValid = true;
            }
        },
        phoneNumber(newValue) {
            if (newValue.length > 0 && this.firstName.length > 0 && this.lastName.length > 0) {
                this.isFormValid = true;
            }
        },
    },
    components: {
        MakundiList,
    },
    computed: {
        // Filter districts based on the selected region
        filteredDistricts() {
            return this.districts.filter(district => district.region_id === this.selectedRegion?.id);
        },
    },
    methods: {
        onRegionChange() {
            // Reset the district selection when region changes
            this.selectedDistrict = null;
        },
        selectChip(chipName) {
            this.selectedChip = this.selectedChip === chipName ? null : chipName;
        },
        showDialog(title, message) {
            this.dialogTitle = title;
            this.dialogMessage = message;
            this.dialogVisible = true;
        },
        handleDialogOk() {
            this.dialogVisible = false;
        },
        async sendWhatsAppMessage(phoneNumber, firstName, lastName) {
            const replaceCountryCode = (phoneNumber) => {
                return phoneNumber.startsWith("0") ? "+255" + phoneNumber.slice(1) : phoneNumber;
            };

            const receivers = [replaceCountryCode(phoneNumber)];
            const contentVariables = { 1: `${firstName.trim()} ${lastName.trim()}` };

            const accountSid = "AC3ac8277891662cea32b31991aca15c99";
            const authToken = "b7ae59ba4accb005436349cda7992400";
            const url = `https://api.twilio.com/2010-04-01/Accounts/${accountSid}/Messages.json`;

            try {
                for (const number of receivers) {
                    const data = new URLSearchParams();
                    data.append("To", `whatsapp:${number}`);
                    data.append("From", "whatsapp:+255674748098");
                    data.append("ContentSid", "HXf97cd1eb9ce82a4867d53352806f8e96");
                    data.append("MessagingServiceSid", "MG5d12d032549a997c009164245a7ce777");
                    data.append("ContentVariables", JSON.stringify(contentVariables));

                    const response = await fetch(url, {
                        method: "POST",
                        headers: {
                            Authorization: "Basic " + btoa(`${accountSid}:${authToken}`),
                            "Content-Type": "application/x-www-form-urlencoded",
                        },
                        body: data,
                    });

                    if (!response.ok) throw new Error("Failed to send message");
                    const responseData = await response.json();
                    console.log(responseData);
                }
            } catch (error) {
                console.error("Error sending WhatsApp message:", error);
            }
        },
        async onFinish() {
            this.processing = true;
            if (!this.gender) {
                this.showDialog("Error", "Bainisha jinsia tafadhali!");
                return;
            }

            try {
                const submissionData = {
                    firstName: this.firstName,
                    lastName: this.lastName,
                    phoneNumber: this.phoneNumber,
                    gender: this.gender,
                    age: this.age,
                    region: this.selectedRegion,
                    district: this.selectedDistrict,
                    category: this.selectedChip,
                    submissionDate: new Date().toISOString(),
                };

                const docRef = await addDoc(collection(db, "applications"), submissionData);
                console.log("Document written with ID:", docRef.id);

                this.showDialog("Success", `Ahsante sana ${this.firstName}, umefanikiwa kutuma taarifa!`);
                this.processing = false;
                await this.sendWhatsAppMessage(this.phoneNumber, this.firstName, this.lastName);

                // Reset form fields
                this.firstName = "";
                this.lastName = "";
                this.phoneNumber = "";
                this.gender = "";
                this.age = "";
                this.selectedRegion = { id: 2, name: "Dar es Salaam" };
                this.selectedDistrict = null;
                this.selectedChip = null;
            } catch (error) {
                console.error("Error adding document:", error);
                this.showDialog("Error", "Failed to submit form. Please try again.");
            }
        },
    },
});
</script>





